/* 전체 배경과 기본 색상 설정 */
body {
  background-color: #1a1a1a;
  color: #e0e0e0;
}

.quote-game {
  max-width: 800px;
  margin: 0 auto;
  padding: 15px;
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
}

.quote-game h1 {
  display: none;
}

/* 박스 스타일 수정 */
.quote-box, .options-container, .start-container {
  background-color: #2c2c2c;
  border-radius: 16px;
  padding: 25px;
  margin: 15px auto;
  max-width: 600px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
  border: 1px solid #3c3c3c;
}

/* 영어 명언 스타일 */
.quote-english h2 {
  color: #fff;
  margin-bottom: 3px;
  line-height: 1.4;
  font-size: 1.5em;
  font-weight: 500;
  word-break: keep-all;
}

.author-section {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 15px;
  color: #888;
}

.author {
  font-style: italic;
  font-size: 0.95em;
  margin-bottom: 5px;
}

.author-info {
  font-size: 0.85em;
  color: #666;
  text-align: right;
}

/* 버튼 스타일 수정 */
.options button {
  width: 100%;
  padding: 15px 20px;
  font-size: 1.2em;
  border: 1px solid #3c3c3c;
  border-radius: 12px;
  background-color: #363636;
  color: #e0e0e0;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: left;
  white-space: normal;
  line-height: 1.6;
  font-weight: 400;
  margin-bottom: 8px;
}

.options {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.options button:hover {
  background-color: #404040;
  transform: translateX(8px);
  border-color: #4c4c4c;
}

.options button.correct {
  background-color: #2e7d32;
  color: white;
  border-color: #2e7d32;
}

.options button.wrong {
  background-color: #c62828;
  color: white;
  border-color: #c62828;
}

.options button:disabled {
  cursor: not-allowed;
  opacity: 0.7;
}

.options button:disabled:hover {
  transform: none;
  border-color: #3c3c3c;
  background-color: #363636;
}

/* 정답과 오답 버튼은 opacity 유지 */
.options button:disabled.correct,
.options button:disabled.wrong {
  opacity: 1;
}

/* 정답/오답 버튼의 hover 효과도 유지 */
.options button:disabled.correct:hover {
  background-color: #2e7d32;
  border-color: #2e7d32;
}

.options button:disabled.wrong:hover {
  background-color: #c62828;
  border-color: #c62828;
}

/* 시작 화면 스타일 수정 */
.start-screen {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
}

.start-container {
  background-color: #2c2c2c;
  border-radius: 16px;
  padding: 35px;
  margin: 25px auto;
  max-width: 500px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  border: 1px solid #3c3c3c;
}

.start-container h2 {
  color: #fff;
  font-weight: 600;
  font-size: 2.2em;
  margin-bottom: 25px;
  letter-spacing: -0.5px;
}

.start-container p {
  color: #a0a0a0;
  margin-bottom: 30px;
  line-height: 1.8;
  font-size: 1.1em;
}

.start-container p br {
  margin-bottom: 8px;
  content: "";
  display: block;
}

.start-container form {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 12px;
  max-width: 400px;
  margin: 0 auto;
}

.start-container input {
  flex: 1;
  padding: 15px 20px;
  font-size: 1.1em;
  background-color: #363636;
  border: 1px solid #3c3c3c;
  border-radius: 12px;
  color: #fff;
  transition: all 0.2s ease;
}

.start-container input:focus {
  outline: none;
  border-color: #4a90e2;
  box-shadow: 0 0 0 2px rgba(74, 144, 226, 0.2);
}

.start-container button {
  padding: 15px 30px;
  font-size: 1.1em;
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: 500;
}

.start-container button:hover {
  background-color: #357abd;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(74, 144, 226, 0.3);
}

.error {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  color: #ff5252;
  font-size: 0.9em;
  width: 100%;
  text-align: center;
}

/* 반응형 디자인 */
@media (max-width: 640px) {
  .start-container {
    margin: 15px;
    padding: 25px 20px;
  }

  .start-container form {
    flex-direction: column;
    gap: 15px;
  }

  .start-container input,
  .start-container button {
    width: 100%;
  }

  .start-hall-of-fame {
    margin: 15px;
    padding: 25px 20px;
  }

  .error {
    bottom: 10px;
  }
}

/* 결과 섹션 */
.result {
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid #3c3c3c;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}

.result .next-button {
  padding: 15px 30px;
  font-size: 1.2em;
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: 500;
  white-space: nowrap;
  order: 2;
}

.result .result-message {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #e0e0e0;
  font-size: 1.1em;
  margin: 0;
  padding: 10px 20px;
  border-radius: 12px;
  font-weight: 500;
  animation: fadeIn 0.3s ease;
  order: 1;
  flex: 1;
}

.result .result-message.correct {
  background-color: rgba(46, 125, 50, 0.2);
  border: 1px solid #2e7d32;
  color: #81c784;
}

.result .result-message.wrong {
  background-color: rgba(198, 40, 40, 0.2);
  border: 1px solid #c62828;
  color: #e57373;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* 모바일 대응 */
@media (max-width: 640px) {
  .result {
    flex-direction: column-reverse;
    gap: 12px;
  }

  .result .next-button {
    width: 100%;
    padding: 12px 25px;
    font-size: 1.1em;
  }

  .result .result-message {
    width: 100%;
    justify-content: center;
  }
}

/* 플레이어 이름 */
.player-name {
  color: #888;
  font-size: 1.1em;
}

/* 에러 메시지 */
.error {
  color: #ff5252;
}

/* 반응형 스타일 유지 */
@media (max-width: 640px) {
  /* 기존 반응형 타일 유지 */
}

/* 게임 통계 스타일 수정 */
.game-stats {
  background-color: #2c2c2c;
  border-radius: 16px;
  padding: 25px;
  margin: 0 auto 15px auto;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  border: 1px solid #3c3c3c;
  max-width: 600px;
  width: 100%;
}

.stats-row {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 15px;
}

.stats-item {
  padding: 12px;
  border-radius: 12px;
  border: 1px solid #3c3c3c;
  transition: all 0.2s ease;
}

/* 각 통계 항목별 다른 배경색 */
.stats-item:nth-child(1) {
  background-color: #2d3748;
}

.stats-item:nth-child(2) {
  background-color: #2d3d4d;
}

.stats-item:nth-child(3) {
  background-color: #2d4352;
}

.stats-item:nth-child(4) {
  background-color: #2d4957;
}

.stats-item:nth-child(5) {
  background-color: #2d4f5c;
}

.stats-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.stats-label {
  display: block;
  color: #888;
  font-size: 0.85em;
  margin-bottom: 5px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  text-align: center;
}

.stats-value {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #e0e0e0;
  font-size: 1.2em;
  font-weight: 600;
  font-family: monospace;
  gap: 4px;
}

.stats-subtext {
  font-size: 0.7em;
  color: #888;
}

/* 모바일 대응 */
@media (max-width: 640px) {
  .game-stats {
    padding: 20px;
    margin: 15px;
  }

  .stats-row {
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;
  }

  .stats-item {
    padding: 10px;
  }

  .stats-value {
    font-size: 1.1em;
  }
}

@media (max-width: 480px) {
  .stats-row {
    grid-template-columns: 1fr;
    gap: 10px;
  }
}

.quote-box {
  position: relative;
}

/* 모바일 반응형 */
